// !SECTION
import { Component, Mixins } from 'vue-property-decorator';
import LotteryCard from './LotteryCard.vue';
import PickerOne from '@/application/Common/View/CommonPickerOne.vue';
import { GameData } from '@/globalInterface';

@Component({
  components: {
    PickerOne
  }
})
export default class LotteryCardTwoSide extends Mixins(LotteryCard) {
  private selectedId?: number = 0;
  private selectedGameType?: string = '';
  private gameList?: GameData[] = [
    {
      id: 7,
      text: '北京PK10',
      gameType: 'pk10',
    },
    {
      id: 10,
      text: 'SG飞艇',
      gameType: 'ssc',
    },
    {
      id: 11,
      text: '极速快乐十分',
      gameType: 'k3',
    }
  ];

  private indexChange(value: GameData) {
    this.selectedGameType = value.gameType;
    this.$emit('index-change', value);
  }
}
